import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Header from './components/header/header.component';
import Home from './pages/home/home.page';
import Practice from './pages/practice/practice.page';
import Projects from './pages/projects/projects.page';
import Workshops from './pages/workshops/workshops.page';
import Workshop1 from './pages/workshops/workshop1.page';
import People from './pages/people/people.page';
import Career from './pages/career/career.page';
import Temporary from './pages/temp-render-page/temporary.page';
import BottomNavigation from './components/bottom-navigation/bottom-navigation.component';
import Footer from './components/footer/footer.component';
import './App.css';

function App() {
  return (
    <div className="App">
    <Router>
      <Header />
        <Switch>
          <Route path='/temp'>
            <Temporary />
          </Route>
          <Route path='/career'>
            <Career />
          </Route>
          <Route path='/people'>
            <People />
          </Route>
          <Route path="/workshop1">
            <Workshop1 />
          </Route>
          <Route path='/projects'>
            <Projects />
          </Route>
          <Route path="/practice">
            <Practice />
          </Route>        
          <Route path="/workshops">
            <Workshops />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>  
        <BottomNavigation /> 
        <Footer />               
      </Router>
    </div>
  );
}

export default App;
